<template>
    <div class="wrapper" id="nav-open">
        <Sidebar></Sidebar>
        <div class="main-panel" data="primary" >
            <!-- orange | primary | blue | green | red | vue | -->
            <NavAuth></NavAuth>
            <div class="content">
              <transition name="fade">
                <keep-alive>
                  <router-view v-show="show" @click="toggleSidebar"/>
                </keep-alive>
              </transition>
            </div>
            <Footer/>
        </div>
    </div>
</template>

<script>
import Footer from "@/components/Layouts/Footer";
import NavAuth from "@/components/Layouts/Navbars/NavAuth";
import Sidebar from "@/components/Layouts/Navbars/Sidebar";

export default {
    name: "TemplateAuth",
    components: {Sidebar, NavAuth, Footer},
    data(){
        return {
            show: true
        }
    },
    methods: {
        toggleSidebar() {
            const elm = document.getElementById('nav-open');
            const element = document.getElementById('toggled');
            if (elm.classList.contains('nav-open')){
                elm.className = '';
                element.className = '';
            }
        },
    }
}
</script>

<style scoped>
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 1.5s ease;
    }

    .fade-enter-from,
    .fade-leave-to {
        opacity: 0;
    }
</style>
