<template>
    <div v-if="$store.state.Auth.auth">
      <Notifications v-if="$store.state.Notification.token" />
      <TemplateAuth></TemplateAuth>
    </div>
    <div v-else>
      <Notifications v-if="$store.state.Notification.message" />
      <TemplateGuest></TemplateGuest>
    </div>
</template>
<script>
import TemplateGuest from "@/components/Layouts/Template/TemplateGuest";
import TemplateAuth from "@/components/Layouts/Template/TemplateAuth";
import Notifications from "@/components/Notifications/Notifications";

export default {
    components: {Notifications, TemplateAuth, TemplateGuest},
}
</script>
