<template>
  <div v-if="this.$store.state.Notification.message" data-notify="container"
       class="col-11 col-md-4 alert alert-with-icon"
       :class="$store.state.Notification.status"
       role="alert" :data-notify-position="$store.state.Notification.position"
       id="notification"
       :alert="this.$store.state.Notification.token"
  >
    <button type="button" aria-hidden="true" class="close" data-notify="dismiss" style="position: absolute; right: 10px; top: 50%; margin-top: -13px; z-index: 1062;" @click="close">
      <i class="tim-icons icon-simple-remove"></i>
    </button>
    <span data-notify="icon" class="tim-icons" :class="$store.state.Notification.icon"></span>
    <span data-notify="title"></span>
    <span data-notify="message">{{ $store.state.Notification.message }}</span>
    <a href="#" target="_blank" data-notify="url"></a>
  </div>
</template>

<script>
export default {
  name: "Notifications",
  data() {
    return {
      token: null
    }
  },
  mounted() {
    let notificationID = document.getElementById("notification")
    if (notificationID) {
      notificationID.setAttribute("style", "visibility: visible; animation: scale-in-ver-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;");
      if (this.$store.state.Notification.static) {
        setTimeout(() => {
          notificationID.setAttribute("style", "visibility: hidden; animation: scale-out-ver-top 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;");
        }, 3000);
      }
    }
  },
  updated: function () {
    this.token = this.$store.state.Notification.token;
  },
  watch: {
    token: function() {
      let notificationID = document.getElementById("notification")
      setTimeout(() => {
        notificationID.setAttribute("style", "visibility: hidden;");
      }, 10);
      setTimeout(() => {
        notificationID.setAttribute("style", "visibility: visible; animation: blink-2 0.9s both;");
      }, 100);
    }
  },
  methods: {
    close() {
      this.$store.state.Notification.token = null
      let notificationID = document.getElementById("notification")
      notificationID.setAttribute("style", "visibility: hidden; animation: scale-out-ver-top 0.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;");
    }
  }
}
</script>


<style>
#notification {
  //visibility: hidden;
  display: inline-block;
  margin: 0 auto;
  position: fixed;
  transition: all 0.5s ease-in-out 0s;
  z-index: 1060;
  top: 20px;
  left: 0;
  right: 0;
}

@keyframes scale-in-ver-top {
  0% {
    transform: scaleY(0);
    transform-origin: 100% 0%;
    opacity: 1;
  }
  100% {
    transform: scaleY(1);
    transform-origin: 100% 0%;
    opacity: 1;
  }
}

@keyframes scale-out-ver-top {
  0% {
    transform: scaleY(1);
    transform-origin: 100% 0%;
    opacity: 1;
  }
  100% {
    transform: scaleY(0);
    transform-origin: 100% 0%;
    opacity: 1;
  }
}

@keyframes blink-2 {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

</style>
