
export default {
    namespaced: true,
    state: {
        message: null,
        position: 'top-center',
        icon: 'icon-bell-55',
        status: 'alert-success',
        static: false,
        token: null
    },
    mutations: {
        SET_NOTIFICATION(state, data) {
            state.message = data.message
            state.position = data.position
            state.icon = data.icon
            state.status = data.status
            state.static = data.static
            state.token = Math.floor(Math.random() * 100)
        }
    },
    actions: {
        getNotification({commit}, data) {
                commit('SET_NOTIFICATION', data);
        }
    }
}
