<template>
    <div>
        <nav
            class="navbar navbar-absolute top-navbar navbar-expand-lg"
            :class="{ 'bg-white': isWhite,  'navbar-transparent': isTransparent }"
        >
            <div class="container-fluid">
                <div class="navbar-wrapper">
                    <div class="navbar-minimize d-inline">
                        <button @click="minimizeSidebar" class="minimize-sidebar btn btn-link btn-just-icon" rel="tooltip" data-original-title="Sidebar toggle" data-placement="right">
                            <i class="tim-icons icon-align-center visible-on-sidebar-regular"></i>
                            <i class="tim-icons icon-bullet-list-67 visible-on-sidebar-mini"></i>
                        </button>
                    </div>
                    <div class="navbar-toggle d-inline" id="toggled">
                        <button type="button" class="navbar-toggler" @click="toggleSidebar">
                            <span class="navbar-toggler-bar bar1"></span>
                            <span class="navbar-toggler-bar bar2"></span>
                            <span class="navbar-toggler-bar bar3"></span>
                        </button>
                    </div>
                    <a class="navbar-brand" href="#">{{ $route.name }}</a>
                </div>
                <button class="navbar-toggler" type="button"  @click="toggleMenu" data-toggle="collapse" data-target="#navigation" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-bar navbar-kebab"></span>
                    <span class="navbar-toggler-bar navbar-kebab"></span>
                    <span class="navbar-toggler-bar navbar-kebab"></span>
                </button>
                <div class="collapse navbar-collapse"
                    :class="{ show: show }"
                     id="navigation">
                    <ul class="navbar-nav ml-auto">
                        <li style="padding-top: 3%;">
                                <span class="d-md-block">{{ $store.state.Auth.user.name }}</span>
                        </li>
                        <li class="dropdown nav-item">
                            <a href="#" class="dropdown-toggle nav-link" data-toggle="dropdown">
                                <div class="notification d-none d-lg-block d-xl-block"></div>
                                <i class="tim-icons icon-bell-55"></i>
                                <p class="d-lg-none">
                                    Notifications
                                </p>
                            </a>
                        </li>
                        <li class="dropdown nav-item">
                          <router-link to="/profile" class="nav-link" data-toggle="dropdown">
                                <div class="photo">
                                    <img :src="'https://ui-avatars.com/api/?color=f4f5f7&background=f09ae2&name='+$store.state.Auth.user.name" alt="Profile Photo">
                                </div>
                               <p class="d-lg-none">
                                 Perfil
                               </p>
                          </router-link>
                        </li>
                        <li class="dropdown nav-item" style="padding-top: 2%;">
                          <Logout />
                        </li>
                        <li class="separator d-lg-none"></li>
                    </ul>
                </div>
            </div>
        </nav>
        <div class="modal modal-search fade" id="searchModal" tabindex="-1" role="dialog" aria-labelledby="searchModal" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <input type="text" class="form-control" id="inlineFormInputGroup" placeholder="SEARCH">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <i class="tim-icons icon-simple-remove"></i>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Logout from "@/components/Layouts/Logout";
export default {
    name: "NavAuth",
    components: {Logout},
  data() {
        return {
            isWhite: false,
            isTransparent: true,
            show: false
        }
    },
    methods: {
        minimizeSidebar() {
            document.body.classList.toggle('sidebar-mini');
            const simulateWindowResize = setInterval(() => {
                window.dispatchEvent(new Event('resize'));
            }, 180);

            setTimeout(() => {
                clearInterval(simulateWindowResize);
            }, 1000);
            this.isMinimized = !this.isMinimized;
        },
        toggleSidebar() {
            const elm = document.getElementById('nav-open');
            const elment = document.getElementById('toggled');

            if(elm.className === 'nav-open'){
                elm.className = '';
                elment.className = '';
            } else {
                elm.className = 'nav-open';
                elment.className = 'toggled';
            }
        },
        toggleMenu(){
            if(this.isTransparent){
                this.isWhite = true;
                this.isTransparent = false;
                setTimeout(() => {
                    this.show = true;
                }, 500);

            }else{
                this.isWhite = false;
                this.isTransparent = true;
                this.show = false;
            }


        }
    }
}
</script>

<style scoped>
.top-navbar {
    top: 0px;
    transition: all 0.5s linear 0s;
}
</style>
