<template>
    <div>
        <div class="navbar-minimize-fixed">
            <button class="minimize-sidebar btn btn-link btn-just-icon">
                <i class="tim-icons icon-align-center visible-on-sidebar-regular text-muted"></i>
                <i class="tim-icons icon-bullet-list-67 visible-on-sidebar-mini text-muted"></i>
            </button>
        </div>
        <div class="sidebar" data="primary" >
            <!-- orange | primary | blue | green | red | vue | -->
            <div class="sidebar-wrapper" id="sidebar">
                <div class="logo">
                    <a href="http://www.creative-tim.com" class="simple-text logo-mini">
                        CT
                    </a>
                    <a href="http://www.creative-tim.com" class="simple-text logo-normal">
                        Creative Tim
                    </a>
                </div>
                <ul class="nav">
                    <li @click="toggleSidebar">
                        <router-link :to="{ name: 'Home'}">
                            <i class="tim-icons icon-chart-pie-36"></i>
                            <p>Dashboard</p>
                        </router-link>
                    </li>
                  <li @click="toggleSidebar">
                    <router-link :to="{ name: 'Profile'}" >
                      <i class="tim-icons icon-single-02"></i>
                      <p>Perfil</p>
                    </router-link>
                  </li>
                    <li class="">
                        <a data-toggle="collapse" class="collapsed" aria-expanded="false" @click="showNav('laravelExamples')" id="menu-laravelExamples" >
                            <i class="fab fa-laravel"></i>
                            <p>
                                Jorge Examples
                                <b class="caret"></b>
                            </p>
                        </a>
                        <div class="collapse" id="laravelExamples">
                            <ul class="nav"
                               >
                                <li @click="toggleSidebar">
                                    <router-link to="/profile"  >
                                        <span class="sidebar-mini-icon">LG</span>
                                        <span class="sidebar-normal">login </span>
                                    </router-link>
                                </li>
                                <li @click="toggleSidebar">
                                    <router-link to="/home" >
                                        <span class="sidebar-mini-icon">PG</span>
                                        <span class="sidebar-normal">Page </span>
                                    </router-link>
                                </li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import PerfectScrollbar from 'perfect-scrollbar';

export default {
    name: "Sidebar",
    mounted() {
        let ps = new PerfectScrollbar('#sidebar');
        ps.update();
    },
    methods:{
        showNav(id){
            const menu = document.getElementById('menu-'+id);
            const element = document.getElementById(id);
            if (element.classList.contains('show')){
                menu.setAttribute("aria-expanded", "false");
                menu.className = 'collapsed';
                element.className = 'collapse';
            }else {
                menu.setAttribute("aria-expanded", "true");
                menu.className = '';
                element.className = 'show';
            }
        },
        toggleSidebar() {
            const elm = document.getElementById('nav-open');
            const element = document.getElementById('toggled');
            if (elm.classList.contains('nav-open')){
                elm.className = '';
                element.className = '';
            }
        },
    }
}
</script>

<style scoped>
    .router-link-active {
        border-bottom: 3px solid #f5f6fa;
    }
</style>
