<template>
  <button type="button" class="btn btn-block btn-sm btn-simple" @click="Logout">
    Cerrar sesión
  </button>
</template>

<script>
import httpClient from "@/plugins/AxiosGlobal";

export default {
  name: "Logout",
  methods: {
    async Logout() {
      await httpClient.post('/logout')
      sessionStorage.clear()
      await window.location.assign("/")
    }
  }
}
</script>
